@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");

:root {
  --black: #222831;
  --white: #dddddd;
  --blue: #4a8cce;
  --red: #f05454;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Inter";
  box-sizing: border-box;
  min-height: 100vh;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--black);
}
.main-div{
  display: flex;
  justify-content: center;
  align-items: center;
  height:55vh;
}
.center-div{
  width: 50%;
  display: flex;
  /* justify-content: center; */
  text-align: center;
  flex-direction: column;

}
.center-div1{
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap:20px;
}
.head{
  display: flex;
  flex-direction: column;
  align-items:center;
}
.head p{
  font-size: 18px;
  text-align: center;
  margin-top: 40px;
  margin-bottom:1%;
  
}
.header{
  border-bottom: 2px solid #dddddd;
  width:60%;
  margin: 0%;
}
#icon{
  padding:10px 5px;
  color:#222831;
}
#icon:hover{
  border:none;
  border-radius: 10px;
}
.btn{
  border:none;
  border-radius: 10px;
  height: auto;
}
.btn1{
  padding: 9px 12px;
  border:none;
  font-size: 16px;
  border-radius: 10px;
  margin-right: 10px;
}
 
.input{
  margin-top: 20px;
  font-family: inherit;
  border: 0;
  border-bottom: 2px solid #dddddd;
  outline: 0;
  font-size: 1.3rem;
  color: #dddddd;
  padding: 10px 0;
  background: transparent;
  transition: border-color 0.2s;
  align-items: center;
}
::placeholder{
  color: #dddddde0;
}

.ul{
  font-family: inherit;
  font-size: 1.3rem;
  background: transparent;
  transition: border-color 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  margin:8% 0px;
}
.button_main{
display: flex;
align-items: center;
justify-content: center;
gap:4%;
}
.ul span{
  width: 80%;
  text-align: left;
  padding-left:6%;
}
li{
  margin-bottom: 10px;
}
#icon1:hover{
  color: #f05454;

}

@media (max-width: 480px)  {
  .center-div{
    width:80%;
  }

.ul{
  margin-left:2%;
} 
.center-div1{
  flex-direction: column;
}
.input{
width: 100%;
}
}

.btndel{
  margin-left: 20%;
}